  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
[hidden] {
  display: none;
}
section {
  width: 100%;
  position: absolute;
  padding: 10px 0;
  background-color: var(--color-bg);
  overflow: hidden;
  -webkit-box-shadow: 0 1px 33px rgba(0, 0, 0, 0.35);
          box-shadow: 0 1px 33px rgba(0, 0, 0, 0.35);
  -webkit-transition: all 0.66s ease-in-out;
  transition: all 0.66s ease-in-out;
}
@media (min-width: 1000px) {
section {
      position: relative;
      overflow-y: scroll;
      height: 100%;
}
}
section::-webkit-scrollbar {
    width: 4px;
}
section::-webkit-scrollbar-track {
    background: #ddd;
    width: 2px;
}
section::-webkit-scrollbar-thumb {
    background: var(--color-accent-contrast-higher);
}
@media (min-width: 1000px) {
section:before {
      content: '';
      display: inline-block;
      position: absolute;
      background: transparent;
      width: 110px;
      height: 110px;
      border-top: 3px solid var(--color-accent-contrast-higher);
      border-left: 3px solid var(--color-accent-contrast-higher);
      left: 20px;
      top: 20px;
      z-index: 0;
}
}
:root, [data-theme="default"] {
  --color-primary: blue;
  /* Цветовой контраст */
  --color-bg: white;
  --color-contrast-lower: hsl(0, 0%, 96%);
  --color-contrast-low: hsl(0, 0%, 90%);
  --color-contrast-medium: hsl(240, 1%, 48%);
  --color-contrast-high: hsl(240, 4%, 20%);
  --color-contrast-higher: black;
}
[data-theme] {
  background-color: var(--color-bg);
  color: var(--color-contrast-high);
}
[data-accent] {
  background-color: var(--color-accent-bg);
  color: var(--color-accent-contrast-high);
}
[data-theme="dark"] {
  --color-primary: red;
  /* Цветовой контраст */
  --color-bg: hsl(0, 0%, 13%);
  --color-contrast-lower: hsl(0, 0%, 16%);
  --color-contrast-low: hsl(0, 0%, 27%);
  --color-contrast-medium: hsl(240, 1%, 57%);
  --color-contrast-high: hsl(0, 0%, 89%);
  --color-contrast-higher: white;
}
[data-accent="default"] {
  --color-accent-primary: blue;
  /* Цветовой контраст */
  --color-accent-bg: white;
  --color-accent-contrast-lower: hsl(0, 0%, 95%);
  --color-accent-contrast-low: hsl(240, 1%, 83%);
  --color-accent-contrast-medium: hsl(240, 1%, 48%);
  --color-accent-contrast-high: hsl(240, 4%, 20%);
  --color-accent-contrast-higher: black;
}
[data-accent="blue"] {
  --color-accent-primary: blue;
  /* Цветовой контраст */
  --color-accent-bg: black;
  --color-accent-contrast-lower: hsl(212, 40%, 95%);
  --color-accent-contrast-low: hsl(212, 45%, 89%);
  --color-accent-contrast-medium: hsl(212, 55%, 79%);
  --color-accent-contrast-high: hsl(212, 65%, 69%);
  --color-accent-contrast-higher: hsl(212, 69%, 59%);
}
